.invitation-codes a {
  background: linear-gradient(139deg, #ffc92f, #ff185c, #f316d5, #b81ed2);
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  color: #fff;
  font-weight: 700;
  border-radius: 5px;
  padding: 0 10px;
  height: 36px;
  display: inline-block;
}
.invitation-codes svg {
  float: left;
  vertical-align: middle;
  display: inline;
  margin-right: 4.5px;
  margin-left: 4.5px;
  padding-top: 7px;
  height: 28px;
}
.invitation-codes span {
  padding: 0 12px;
  vertical-align: middle;
  float: left;
  line-height: 34px;
}

